ul{
	margin-left: 0;
	padding-left: 0;
}
li{
	list-style-type: none;
}
.card-page__wrapper{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 70%;
	margin: 60px 20px;
}
.card-page__img{
	-ms-flex-preferred-size: 35%;
	    flex-basis: 35%;
	height: 100%;
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}
 .card-page__content{
 	-ms-flex-preferred-size: 40%;
 	    flex-basis: 40%;
 	max-height: 80vh;
 }
 .card-page__title{
	margin: 2px 0;
	color: #3172db;
 }
 .card-page__param{
 	line-height: 30px;
 }
 .card-page__text-title{
 	font-weight: bold;
 	color: #ccc;
 }
.card-page__text{
	margin-top: 40px;
}


