.cards{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	width: 70%;
	position: relative;
}
.load-more{
	position: absolute;
	bottom: -50px;
	right: 0;
	background-color: #3172db;
	width: 200px;
	height: 42px;
	line-height: 40px;
	color: #fff;
	text-align: center;
	border-radius: 5px;
	text-decoration: none;
}
.load-more:hover{
	background-color: #fb5c3d;
}



